"use client"

import React, { useState, useEffect } from "react"
import { LayoutWrapper } from "@/components/layout-wrapper"
import { Plus, CalendarIcon } from "lucide-react"
import { getCurrentUser } from "@/lib/constants"
import { Calendar } from "@/components/ui/calendar"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"
import { format } from "date-fns"

/* ---------- TYPES ---------- */
type PatientRole = "student" | "teaching_staff" | "non_teaching_staff"
type Program = "CICT" | "CBME"
type NonTeachingCategory =
  | "Administration"
  | "Accounting"
  | "Human Resources"
  | "Student Service"
  | "Library"
  | "Maintenance"
  | "Security"
  | "Supply"
  | "Clinic"

interface Patient {
  id: string
  firstName: string
  lastName: string
  middleName?: string
  suffix?: string
  dateOfBirth: string
  gender: "Male" | "Female"
  phone: string
  email?: string
  address?: string
  role: PatientRole
  idNumber: string
  program?: Program
  course?: string
  yearLevel?: number
  block?: number
  department?: Program
  staffCategory?: NonTeachingCategory
  pastIllnesses?: string
  surgeries?: string
  currentMedication?: string
  allergies?: string
  medicalNotes?: string
  primaryContactName?: string
  primaryContactRelationship?: string
  primaryContactPhone?: string
  primaryContactAddress?: string
  secondaryContactName?: string
  secondaryContactRelationship?: string
  secondaryContactPhone?: string
  secondaryContactAddress?: string
  attachments?: string
}

interface FormData {
  firstName: string
  middleName: string
  lastName: string
  suffix: string
  dateOfBirth: string
  gender: "Male" | "Female"
  phone: string
  email: string
  address: string
  role: PatientRole
  idNumber: string
  program: Program | ""
  course: string
  yearLevel: number | ""
  block: number | ""
  department: Program | ""
  staffCategory: NonTeachingCategory | ""
  pastIllnesses: string
  surgeries: string
  currentMedication: string
  allergies: string
  medicalNotes: string
  primaryContactName: string
  primaryContactRelationship: string
  primaryContactPhone: string
  primaryContactAddress: string
  secondaryContactName: string
  secondaryContactRelationship: string
  secondaryContactPhone: string
  secondaryContactAddress: string
  attachments: string
}

const COURSE_OPTIONS: Record<Program, string[]> = {
  CICT: ["BSIT", "BSCS", "BSIS", "BTVTED"],
  CBME: ["BSA", "BSAIS", "BPA", "BSE"],
}

export default function PatientsPage() {
  const { toast } = useToast()

  const [patients, setPatients] = useState<Patient[]>([])
  const [showForm, setShowForm] = useState(false)
  const [editingPatient, setEditingPatient] = useState<Patient | null>(null)
  const [user, setUser] = useState<{ id: string } | null>(null)

  const [selectedDateOfBirth, setSelectedDateOfBirth] = useState<Date>()
  const [availableCourses, setAvailableCourses] = useState<string[]>([])

  const [formData, setFormData] = useState<FormData>({
    firstName: "",
    middleName: "",
    lastName: "",
    suffix: "",
    dateOfBirth: "",
    gender: "Male",
    phone: "",
    email: "",
    address: "",
    role: "student",
    idNumber: "",
    program: "",
    course: "",
    yearLevel: "",
    block: "",
    department: "",
    staffCategory: "",
    pastIllnesses: "",
    surgeries: "",
    currentMedication: "",
    allergies: "",
    medicalNotes: "",
    primaryContactName: "",
    primaryContactRelationship: "",
    primaryContactPhone: "",
    primaryContactAddress: "",
    secondaryContactName: "",
    secondaryContactRelationship: "",
    secondaryContactPhone: "",
    secondaryContactAddress: "",
    attachments: "",
  })

  /* ---------- EFFECTS ---------- */
  useEffect(() => {
    const u = getCurrentUser()
    if (u) setUser(u)
  }, [])

  useEffect(() => {
    if (formData.program && formData.program in COURSE_OPTIONS) {
      setAvailableCourses(COURSE_OPTIONS[formData.program as Program])
    } else {
      setAvailableCourses([])
    }
  }, [formData.program])

  /* ---------- FETCH ---------- */
  const fetchPatients = async () => {
    if (!user) return
    const res = await fetch("/api/patients", { headers: { "x-user-id": user.id } })
    const data = await res.json()
    setPatients(data.patients || [])
  }

  useEffect(() => {
    if (user) fetchPatients()
  }, [user])

  /* ---------- HANDLERS ---------- */
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleAddOrEdit = async (e: React.FormEvent) => {
    e.preventDefault()

    if (!formData.firstName || !formData.lastName || !selectedDateOfBirth) {
      toast({ title: "Missing required fields", variant: "destructive" })
      return
    }

    const payload = {
      ...formData,
      dateOfBirth: selectedDateOfBirth.toISOString().split("T")[0],
      id: editingPatient?.id,
    }

    const res = await fetch("/api/patients", {
      method: editingPatient ? "PUT" : "POST",
      headers: {
        "Content-Type": "application/json",
        "x-user-id": user?.id || "",
      },
      body: JSON.stringify(payload),
    })

    if (!res.ok) {
      const err = await res.json().catch(() => ({}))
      toast({ title: "Failed to save patient", description: err.error, variant: "destructive" })
      return
    }

    toast({ title: "Patient saved successfully" })
    setShowForm(false)
    setEditingPatient(null)
    setFormData({
      firstName: "",
      middleName: "",
      lastName: "",
      suffix: "",
      dateOfBirth: "",
      gender: "Male",
      phone: "",
      email: "",
      address: "",
      role: "student",
      idNumber: "",
      program: "",
      course: "",
      yearLevel: "",
      block: "",
      department: "",
      staffCategory: "",
      pastIllnesses: "",
      surgeries: "",
      currentMedication: "",
      allergies: "",
      medicalNotes: "",
      primaryContactName: "",
      primaryContactRelationship: "",
      primaryContactPhone: "",
      primaryContactAddress: "",
      secondaryContactName: "",
      secondaryContactRelationship: "",
      secondaryContactPhone: "",
      secondaryContactAddress: "",
      attachments: "",
    })
    setSelectedDateOfBirth(undefined)
    fetchPatients()
  }

  /* ---------- UI ---------- */
  return (
    <LayoutWrapper>
      <div className="space-y-6">
        <div className="flex justify-between">
          <h1 className="text-3xl font-bold">Patient Management</h1>
          <Button onClick={() => setShowForm(true)}>
            <Plus className="h-4 w-4 mr-2" /> Add Patient
          </Button>
        </div>

        {showForm && (
          <form onSubmit={handleAddOrEdit} className="bg-white p-6 border rounded-lg">
            <h2 className="text-xl font-semibold mb-4">
              {editingPatient ? "Edit Patient" : "Add Patient"}
            </h2>

            <input
              name="firstName"
              value={formData.firstName}
              onChange={handleInputChange}
              placeholder="First Name"
              className="border p-2 w-full mb-2"
            />

            <input
              name="lastName"
              value={formData.lastName}
              onChange={handleInputChange}
              placeholder="Last Name"
              className="border p-2 w-full mb-2"
            />

            <Popover>
              <PopoverTrigger asChild>
                <Button variant="outline" className="w-full justify-start">
                  <CalendarIcon className="mr-2 h-4 w-4" />
                  {selectedDateOfBirth ? format(selectedDateOfBirth, "PPP") : "Pick date"}
                </Button>
              </PopoverTrigger>
              <PopoverContent>
                <Calendar
                  mode="single"
                  selected={selectedDateOfBirth}
                  onSelect={setSelectedDateOfBirth}
                />
              </PopoverContent>
            </Popover>

            <div className="flex justify-end gap-4 mt-6">
              <Button type="button" variant="outline" onClick={() => setShowForm(false)}>
                Cancel
              </Button>
              <Button type="submit">
                {editingPatient ? "Update Patient" : "Add Patient"}
              </Button>
            </div>
          </form>
        )}
      </div>
    </LayoutWrapper>
  )
}
